.HelpContainer {
    position: absolute;
    background-color: White;
    text-align: center;
    display: flex;
    flex-direction: column;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 700px;
    height: 700px;
    padding: 20px;
    border: 2px solid rgb(38, 63, 100);
    border-radius: 10px;
}

.Background {
    position: absolute;
    background-size: cover;
    backdrop-filter: blur(1px);
    height: 100vh;
    width: 100%;
    z-index: 1;
}

.Title {
    color: rgb(38, 63, 100);
    font-weight: 600;
}

.Title {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.NavBar {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.Card {
    width: 100%;
    height: 100%;
}